import { EntityState, EntityStateAdapter, IdSelector, Update } from './models';
import { createStateOperator } from './state_adapter';
/**
 * @template T
 * @param {?} selectId
 * @return {?}
 */
export function createUnsortedStateAdapter<T>(
  selectId: IdSelector<T>
): EntityStateAdapter<T> {
  type R = EntityState<T>;
/**
 * @param {?} entity
 * @param {?} state
 * @return {?}
 */
function addOneMutably(entity: T, state: R): boolean {
    const /** @type {?} */ key = selectId(entity);

    if (key in state.entities) {
      return false;
    }

    state.ids.push(key);
    state.entities[key] = entity;

    return true;
  }
/**
 * @param {?} entities
 * @param {?} state
 * @return {?}
 */
function addManyMutably(entities: T[], state: R): boolean {
    let /** @type {?} */ didMutate = false;

    for (let /** @type {?} */ index in entities) {
      didMutate = addOneMutably(entities[index], state) || didMutate;
    }

    return didMutate;
  }
/**
 * @param {?} entities
 * @param {?} state
 * @return {?}
 */
function addAllMutably(entities: T[], state: R): boolean {
    state.ids = [];
    state.entities = {};

    addManyMutably(entities, state);

    return true;
  }
/**
 * @param {?} key
 * @param {?} state
 * @return {?}
 */
function removeOneMutably(key: string, state: R): boolean {
    return removeManyMutably([key], state);
  }
/**
 * @param {?} keys
 * @param {?} state
 * @return {?}
 */
function removeManyMutably(keys: string[], state: R): boolean {
    const /** @type {?} */ didMutate =
      keys
        .filter(key => key in state.entities)
        .map(key => delete state.entities[key]).length > 0;

    if (didMutate) {
      state.ids = state.ids.filter(id => id in state.entities);
    }

    return didMutate;
  }
/**
 * @template S
 * @param {?} state
 * @return {?}
 */
function removeAll<S extends R>(state: S): S {
    return Object.assign({}, state, {
      ids: [],
      entities: {},
    });
  }
/**
 * @param {?} keys
 * @param {?} update
 * @param {?} state
 * @return {?}
 */
function takeNewKey(
    keys: { [id: string]: string },
    update: Update<T>,
    state: R
  ): void {
    const /** @type {?} */ original = state.entities[update.id];
    const /** @type {?} */ updated: T = Object.assign({}, original, update.changes);
    const /** @type {?} */ newKey = selectId(updated);

    if (newKey !== update.id) {
      keys[update.id] = newKey;
      delete state.entities[update.id];
    }

    state.entities[newKey] = updated;
  }
/**
 * @param {?} update
 * @param {?} state
 * @return {?}
 */
function updateOneMutably(update: Update<T>, state: R): boolean {
    return updateManyMutably([update], state);
  }
/**
 * @param {?} updates
 * @param {?} state
 * @return {?}
 */
function updateManyMutably(updates: Update<T>[], state: R): boolean {
    const /** @type {?} */ newKeys: { [id: string]: string } = {};

    const /** @type {?} */ didMutate =
      updates
        .filter(update => update.id in state.entities)
        .map(update => takeNewKey(newKeys, update, state)).length > 0;

    if (didMutate) {
      state.ids = state.ids.map(id => newKeys[id] || id);
    }

    return didMutate;
  }

  return {
    removeAll,
    addOne: createStateOperator(addOneMutably),
    addMany: createStateOperator(addManyMutably),
    addAll: createStateOperator(addAllMutably),
    updateOne: createStateOperator(updateOneMutably),
    updateMany: createStateOperator(updateManyMutably),
    removeOne: createStateOperator(removeOneMutably),
    removeMany: createStateOperator(removeManyMutably),
  };
}
